[data-alc-theme="light"] alc-datatable {
  --color-header: theme('colors.gray-cool.10');
  --color-header-sorting: theme('colors.gray.20');
  --color-header-hover: theme('colors.gray.30');

  --color-row-odd: theme('colors.gray.4');
  --color-row-odd-sorting: theme('colors.gray.10');
  --color-row-even: theme('colors.white');
  --color-row-even-sorting: theme('colors.gray.5');
  --color-row-hover: theme('colors.gray.10');

  --color-header-border: theme('colors.gray.40');
  --color-body-border: theme('colors.gray.20');
}

[data-alc-theme="dark"] alc-datatable {
  --color-header: theme('colors.gray.80');
  --color-header-sorting: theme('colors.gray.95');
  --color-header-hover: theme('colors.gray.70');

  --color-row-odd: theme('colors.black');
  --color-row-odd-sorting: theme('colors.gray.80');
  --color-row-even: theme('colors.transparent');
  --color-row-even-sorting: theme('colors.gray.70');
  --color-row-hover: theme('colors.gray.60');

  --color-header-border: theme('colors.gray.70');
  --color-body-border: theme('colors.gray.70');

}

alc-datatable {
  @apply relative block;
}


.dataTables_wrapper {
  --shadow-color:  theme('colors.gray.30');
  /* Relative para o sombreamento */
  @apply mt-2 relative;
}

.alc-datatable__wrapper {
  @apply overflow-auto transition-shadow overflow-x-auto shadow-none;
}

/* Sombreamento */
.alc-datatable__wrapper.has-scroll-end:after {
  @apply block content-["\200C"];
  @apply w-3 h-full absolute top-0 right-0 z-10;
  box-shadow: inset -8px 0px 8px -8px var(--shadow-color);
}
/* Sombreamento */
.alc-datatable__wrapper.has-scroll-start:before {
  @apply block content-["\200C"];
  @apply w-3 h-full absolute top-0 left-0 z-10;
  box-shadow: inset 8px 0px 8px -8px var(--shadow-color);
}

.alc-table {
  @apply w-full;
}

/* Espaamento entre o thead e tbody */
.alc-table tbody:before {
  @apply block content-["\200C"] leading-3;
}

.alc-table__header-row {
  @apply text-left bg-[--color-header];
}

.alc-table__header-cell {
  @apply p-3 border-r last:border-none border-[--color-header-border] text-base;
  /* Ajustes para os icones */
  @apply align-top relative pr-7;
}

.alc-table__header-cell.sorting {
  @apply cursor-pointer hover:bg-[--color-header-hover];
}

.sorting_asc.alc-table__header-cell,
.sorting_desc.alc-table__header-cell {
  @apply bg-[--color-header-sorting] hover:bg-[--color-header-hover]
}

.alc-datatable__sort-icon {
  @apply absolute top-3 right-1.5;
}

.alc-table__row.odd {
  @apply bg-[--color-row-odd];
}

.alc-table__row.even {
  @apply bg-[--color-row-even];
}

.alc-table__row.odd .alc-table__cell.sorting_1 {
  @apply bg-[--color-row-odd-sorting];
}

.alc-table__row.even .alc-table__cell.sorting_1 {
  @apply bg-[--color-row-even-sorting];
}

.alc-table__row:hover,
.alc-table__row:hover .alc-table__cell.sorting_1 {
  @apply bg-[--color-row-hover];
}

.alc-table__cell {
  @apply p-3 border-r last:border-none border-[--color-body-border];
}

.alc-field__input.alc-datatable__select-length {
  @apply inline-block w-auto mx-2;
}

.alc-table__pagination {
  /* Itens podem estar empilhados (se faltar espao), e ficam centralizados no continer */
  @apply flex flex-wrap items-center justify-center mt-2 gap-2;
  /* Itens lado a lado, nas extremidades do continer */
  @apply sm:justify-between;
}

/* controles de ordenao e filtro */
.alc-datatable__controls {
  @apply flex flex-col items-start sm:flex-row sm:justify-between sm:items-end gap-2;
}

.alc-datatable__controls-order {
  @apply flex flex-wrap gap-2;
}

.alc-datatable__controls-search {
  @apply w-full sm:w-auto;
}

.alc-datatable__controls-search alc-field {
  @apply flex flex-col sm:flex-row sm:items-center;
}

.alc-datatable__processing {
  @apply absolute inset-0 pointer-events-none z-50;
}

.alc-datatable__processing .alc-loading__overlay-container {
  @apply flex items-center justify-center bg-transparent;
}

.alc-datatable__processing .alc-loading__card-container {
  @apply border border-[--alc-color-border] border-solid shadow-lg;
}